<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
      
        <el-form-item label="物料名称">
          <el-input
            v-model="listQuery.matter_name"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-form-item label="物料类型">
          <el-select v-model="listQuery.matter_category" placeholder="请选择">
            <el-option label="全部" value=""></el-option>
            <el-option label="A类" value="A类"></el-option>
            <el-option label="B类" value="B类"></el-option>
            <el-option label="其他" value="其他"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="操作人员">
           <el-select
            v-model="listQuery.operate_user"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in PersonCodeNameList"
              :key="index + ' dialog'"
              :label="item.user_name"
              :value="item.user_code"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="状态">
          <el-select v-model="listQuery.status" placeholder="请选择">
            <el-option label="全部" value=""></el-option>
            <el-option label="已使用" value="已使用"></el-option>
            <el-option label="未使用" value="未使用"></el-option>
          </el-select>
        </el-form-item> -->
        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>物料列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <!-- 新增物料弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-form-item label="物料名称" prop="matter_code">
          <el-select
            v-model="dialogForm.matter_code"
            placeholder="请选择"
            style="width: 100%"
            :disabled="matter_code_disabled"
          >
            <el-option
              v-for="(item, index) in BomMatterCodeNameList"
              :key="index + ' search'"
              :label="item.matter_name"
              :value="item.matter_code"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="数量" prop="matter_count">
          <el-input v-model.number="dialogForm.matter_count"></el-input>
        </el-form-item>

        <el-form-item label="入库日期" prop="product_time">
          <el-date-picker
            v-model="dialogForm.product_time"
            type="datetime"
            style="width: 100%"
            value-format="yyyy-MM-dd HH:mm:ss"
            format="yyyy-MM-dd HH:mm:ss"
            placeholder="选择日期"
          >
          </el-date-picker>
        </el-form-item>
        
        <el-form-item v-if="dialogTitle=='修改物料'" label="入库人" prop="matter_count">
          <span> {{dialogForm.operate_user}} </span>
          <!-- <span> {{dialogForm.operate_user_name}} </span> -->
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "MaterialList",
  components: {
    ELTable,
  },
  computed: {
    ...mapState(["BomMatterCodeNameList","PersonCodeNameList","CurrentUserInfo"]),
  },
  data() {
    return {
      // 筛选
       listQuery: {
        matter_name: "",
        matter_category: "",
        status: "",
        operate_user: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        matter_name: "",
        matter_category: "",
        status: "",
        operate_user: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      // 新增物料弹出层
      dialogTitle: "",
      matter_code_disabled: false,
      dialogFlag: false,
      dialogForm: {
        matter_code: "",
        matter_name: "",
        operate_user: "",
        operate_user_name: "",
        matter_count: "",
        status: "未使用",
        product_time: "",
      },
      dialogFormBase: {
        matter_code: "",
        matter_name: "",
        operate_user: "",
        operate_user_name: "",
        matter_count: "",
        status: "未使用",
        product_time: "",
      },
      dialogFormRules: {
        matter_name: [
          { required: true, message: "请输入物料名称", trigger: "blur" },
        ],
        matter_count: [
          { required: true, message: "请输入数量", trigger: "blur" },
          { type: "number", message: "数量必须为数值" },
        ],
        product_time: [
          { required: true, message: "请选择入库日期", trigger: "change" },
        ],
      },
      // 表格列
      tableCol: [
        {
          label: "物料名称",
          field: "matter_name",
          align: "center",
        },
        {
          label: "规格型号",
          field: "rule",
          align: "center",
        },
        {
          label: "类型",
          field: "matter_category",
          align: "center",
        },
        {
          label: "数量",
          field: "matter_count",
          align: "center",
        },
        {
          label: "操作人员",
          field: "operate_user",
          // field: "operate_user_name",
          align: "center",
        },
        {
          label: "入库日期",
          field: "product_time",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
     
      ispagination: true, // 分页是否显示
    };
  },
  methods: {
    ...mapActions(["getLogisticBomMatterCodeName", "getLogisticPersonCodeName"]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticPersonMatter(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticPersonMatter(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticPersonMatter(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticPersonMatter(this.listQuery);
    },
    // 新增
    addBtn() {
      this.dialogTitle = "新增物料";
      this.dialogFlag = true;
      this.matter_code_disabled = false;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },

    // 物料清单列表 查询
    getLogisticPersonMatter(params) {
      baseManagement.getLogisticPersonMatter(params).then((res) => {
        console.log("物料清单列表 查询", res);
        if (res.code == 0) {
            let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            this.PersonCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.operate_user == CodeNameListItem.user_code) {
                tableDataItem.operate_user_name = CodeNameListItem.user_name;
              }
            });
          });
          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 物料清单列表 新增
    postLogisticPersonMatter(params) {
      baseManagement.postLogisticPersonMatter(params).then((res) => {
        console.log("物料清单列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticPersonMatter(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 物料清单列表 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改物料";
      this.dialogFlag = true;
      this.matter_code_disabled = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });

      this.PersonCodeNameList.forEach((CodeNameListItem) => {
        if (this.dialogForm.operate_user == CodeNameListItem.user_code) {
          this.dialogForm.operate_user_name = CodeNameListItem.user_name;
        }
      });
      this.dialogForm = {
        matter_code: rowData.matter_code,
        matter_count: rowData.matter_count,
        operate_user: rowData.operate_user,
        operate_user_name: rowData.operate_user_name,
        product_time: rowData.product_time,
      };
      
    },
    getLogisticPutPersonMatter(params) {
      baseManagement.getLogisticPutPersonMatter(params).then((res) => {
        console.log("物料清单列表 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticPersonMatter(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 物料清单列表 弹出层确定
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增物料") {
            console.log("新增物料", this.dialogForm);
            this.postLogisticPersonMatter(this.dialogForm);
          } else {
            console.log("修改物料", this.dialogForm);
            this.getLogisticPutPersonMatter(this.dialogForm);
          }
        }
      });
    },

    // 物料清单列表 删除
    getLogisticDeletePersonMatter(params) {
      baseManagement.getLogisticDeletePersonMatter(params).then((res) => {
        console.log("物料清单列表 删除", res);
        if (res.code == 0) {
          this.getLogisticPersonMatter(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.getLogisticDeletePersonMatter({
          matter_code: rowData.matter_code,
        });
      });
    },
  },
  mounted() {
    // 产品 物料 名称和编码接口
    this.getLogisticBomMatterCodeName();
      // 人员内码和人员名称 查询
       this.getLogisticPersonCodeName();
    // 物料清单列表 查询
    this.getLogisticPersonMatter(this.listQuery);
  },
};
</script>

